#use-velo {
  padding-top: 120px;
}

.use-velo__title {
  color: #fff;
  max-width: 600px;
  opacity: 0;

  span {
    color: #ffff54;
  }
}

.use-velo__list {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  margin-top: 200px;
}

.use-velo__list-item {
  list-style: decimal;
  font-weight: normal;
  font-size: 40px;
  line-height: 48px;
  color: #fff;
  display: flex;
  flex-direction: column;
}

// .use-velo__list-item::before {
//   content: counter(my-counter);
//   counter-increment: my-counter;
// }

.use-velo__list-description {
  color: #fff;
  text-align: center;
  font-weight: normal;
  font-size: 23px;
  line-height: 28px;
  margin-top: 20px;
}

@media (max-width: 992px) {
  #use-velo {
    padding-top: 0;
    background: #021141;
  }

  #use-velo::after {
    content: '';
    width: 100%;
    height: 50px;
    background: #a8a11b;
    display: block;
    margin-bottom: 20px;
    clip-path: polygon(83% 58%, 100% 0, 100% 100%, 0 100%, 0 0);
  }

  #use-velo::before {
    content: '';
    width: 100%;
    height: 50px;
    background: #0c2c76;
    display: block;
    margin-bottom: 20px;
    clip-path: polygon(100% 0, 100% 100%, 36% 16%, 0 100%, 0 0);
  }

  .use-velo__list {
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .use-velo__list-item {
    margin-bottom: 50px;
  }
}

@media (max-width: 576px) {
  .use-velo__list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .use-velo__list-description {
    font-size: 14px;
    line-height: 20px;
  }

  .use-velo__list-item_number {
    font-size: 24px;
  }

  .swiper-slide > .use-velo__list-description {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide > .use-velo__list-item_number {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide-active > .use-velo__list-description {
    opacity: 1;
  }

  .swiper-slide-active > .use-velo__list-item_number {
    opacity: 1;
  }
}
