html {
  height: 100%; // Footer at bottom
  font-size: 62.5%;
  overflow-x: hidden;
}

body {
  display: flex; // Footer at bottom
  flex-direction: column; // Footer at bottom
  height: 100%; // Footer at bottom
  color: #000;
  font-size: 1.4rem;
  line-height: 1.6rem;
  background: #fff;
  font-family: 'Velo Sans', sans-serif;
  overflow-x: hidden;
}

.header {
  flex-shrink: 0; // Footer at bottom
}

.main {
  position: relative;
  flex: 1 0 auto; // Footer at bottom
  background: url('../images/bg-min.png') no-repeat center top;
}

.footer {
  z-index: 10;
  flex-shrink: 0; // Footer at bottom
  margin-top: auto;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: none;

  table & {
    max-width: none;
  }
}

.container {
  max-width: 1250px;
  margin: 0 auto;
}

.title {
  font-weight: bold;
  font-size: 48px;
  line-height: 48px;
  letter-spacing: -1.28058px;
  text-transform: uppercase;
}

@media (max-width: 1300px) {
  .container {
    width: 95%;
  }
}

@media (max-width: 992px) {
  .main {
    background: none;
  }
}

@media (max-width: 992px) {
  .title {
    font-size: 36px;
    line-height: 39px;
  }
}
