#medium-user {
  padding-top: 380px;
}

.velo-goods__title {
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  text-transform: uppercase;
  color: #37b0ec;
  margin-bottom: 50px;
}

.velo-goods-much {
  display: flex;
  flex-wrap: wrap;
}

.velo-goods .swiper-scrollbar-drag {
  background-color: #000;
}

.swiper-scrollbar-lock {
  display: block !important;
}

@media (max-width: 992px) {
  #medium-user {
    padding-top: 0;
  }

  #medium-user::before {
    content: '';
    width: 100%;
    height: 50px;
    background: #34b2ed;
    display: block;
    clip-path: polygon(100% 0, 100% 100%, 39% 43%, 0 100%, 0 0);
  }

  .medium-user {
    margin-top: 50px;
  }

  .velo-goods-much {
    justify-content: space-between;
  }
}

.medium-slider .swiper-pagination-bullet:first-child {
  width: 45px;
  height: 33px;
  background: url('../images/slider2Small1.png');
  opacity: 1;
}

.medium-slider .swiper-pagination-bullet:nth-child(2) {
  width: 45px;
  height: 33px;
  background: url('../images/slider2Small2.png');
  opacity: 1;
}

.medium-slider .swiper-pagination-bullet:nth-child(3) {
  width: 45px;
  height: 33px;
  background: url('../images/slider2Small3.png');
  opacity: 1;
}

.medium-slider .swiper-pagination-bullet:nth-child(4) {
  width: 45px;
  height: 33px;
  background: url('../images/slider2Small4.png');
  opacity: 1;
}

.medium-slider .swiper-pagination-bullet:last-child {
  width: 45px;
  height: 33px;
  background: url('../images/slider2Small5.png');
  opacity: 1;
}

@media (max-width: 690px) {
  .swiper-scrollbar {
    display: none;
  }
}

@media (min-width: 1500px) {
  .velo-good__img {
      width: 80%;
  }
}

.slick-arrow{
  display: none;
} 