#best-velo {
  padding-top: 75px;
}

.best-velo__title {
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  text-transform: uppercase;
  color: #37b0ec;
}

.best-velo__subtitle {
  font-size: 18px;
  line-height: 1;
  margin-top: 20px;
}

.velo-goods {
  display: flex;
  margin-top: 10px;
}

.velo-goods__item {
  margin-bottom: 50px;
}

.velo-good__title {
  font-weight: bold;
  font-size: 23px;
  line-height: 28px;
  display: flex;
  align-items: flex-end;
  text-transform: uppercase;
  color: #000;
  margin-top: 30px;
}

.velo-streight__block {
  display: flex;
  align-items: center;
  margin-top: 30px;
}

.velo-streight {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #bbb;
  margin-right: 11px;
}

.velo-streight.active {
  background: #d12c38;
}

.velo-good__streight span {
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  color: #757778;
  display: flex;
  align-items: center;
}

.velo-good__streight span::before {
  content: '';
  height: 20px;
  width: 1px;
  background: #757778;
  display: flex;
  align-items: center;
  margin-right: 11px;
}

.velo-good__img {
  text-align: center;
}

// .velo-good__img  img{
//   height: 220px;
//   width: 280px;
// }

.velo-good__taste {
  font-size: 22px;
  line-height: 26px;
  color: #231f20;
  margin-top: 30px;
}

@media (max-width: 992px) {
  #best-velo::after {
    content: '';
    width: 100%;
    height: 50px;
    background: #122f73;
    display: block;
    clip-path: polygon(83% 58%, 100% 0, 100% 100%, 0 100%, 0 0);
  }

  .velo-goods {
    flex-wrap: wrap;
  }

  .velo-goods__item {
    margin-right: 0;
  }
}

@media (max-width: 690px) {
  .velo-goods__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .velo-good__taste {
    text-align: center;
  }
}

.swiper-scrollbar__mobile {
  display: none !important;
}

@media (max-width: 690px) {
  .swiper-scrollbar__mobile {
    display: block !important;
  }
}

.swiper-pagination {
  position: static;
  display: none;
  margin-bottom: 35px;
}

.best-velo-slider .swiper-pagination-bullet:first-child {
  width: 45px;
  height: 33px;
  background: url('../images/slider1Small1.png');
  opacity: 1;
}

.best-velo-slider .swiper-pagination-bullet:last-child {
  width: 45px;
  height: 33px;
  background: url('../images/slider1Small2.png');
  opacity: 1;
}

@media (max-width: 690px) {
  .swiper-pagination {
    display: block;
  }

  .velo-streight {
    width: 10px;
    height: 10px;
    margin-right: 3px;
  }

  .velo-good__streight span {
    font-size: 14px;
  }

  .velo-good__title {
    text-align: center;
  }

  .velo-good__img {
    width: 85%;
  }

  .velo-good__streight span::before {
    height: 45px;
    margin-left: 11px;
  }

  .swiper-slide > .velo-good__title {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide-active > .velo-good__title {
    opacity: 1;
    transition: 0.5s;
  }

  .swiper-slide > .velo-good__streight {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide-active > .velo-good__streight {
    opacity: 1;
    transition: 0.5s;
  }

  .swiper-slide > .velo-good__taste {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide-active > .velo-good__taste {
    opacity: 1;
    transition: 0.5s;
  }
}


