#filter-block {
  padding-top: 280px;
}

.filter-title {
  max-width: 575px;

  span {
    color: #a7a021;
  }
}

.filter-btn__block {
  margin-top: 50px;
}

.filter-btn {
  width: 204px;
  padding: 17px 25px;
  background: #c5be42;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  border: none;
  text-decoration: none;
}

.filter-btn:last-child {
  background: #37b0ec;
  margin-left: 17px;
}

@media (max-width: 992px) {
  #filter-block {
    padding-top: 20px;
  }
}

@media (max-width: 460px) {
  .filter-btn__block {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
  }

  .filter-btn {
    margin-top: 20px;
    width: 100%;
  }

  .filter-btn:last-child {
    margin-left: 0;
  }
}
