header {
  padding: 40px 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar a {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 15px;
  letter-spacing: 0.5px;
  color: #041e42;
  text-decoration: none;
  padding: 20px;
}

.header-btn a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: inherit;
  height: inherit;
}

.navbar a:hover {
  color: #00adbb;
}

.header-btn {
  width: 33px;
  height: 33px;
  border-radius: 50%;
  background: #5862bf;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-btns__block {
  display: flex;
  align-items: center;
}

.burger {
  display: none;
  width: 32px;
  height: 32px;
  margin-left: 15px;
}

.burger.active::before {
  content: '';
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #e5e5e5;
  opacity: 0.5;
  z-index: 3;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

@media (max-width: 992px) {
  .navbar {
    display: none;
  }

  .burger {
    display: flex;
  }
}
