#velo-description {
  padding-top: 170px;
}

.velo-description {
  font-weight: 500;
  font-size: 40px;
  line-height: 48px;
  color: #fff;
  max-width: 850px;
}

.velo-description span {
  color: #ffff54;
  font-weight: bold;
}

@media (max-width: 992px) {
  #velo-description {
    padding-top: 50px;
    background: #122f73;
    padding-bottom: 50px;
  }
}

@media (max-width: 576px) {
  .velo-description {
    font-weight: 500;
    font-size: 20px;
    line-height: 25px;
  }
}
