.footer {
  background: linear-gradient(85deg, #31569c 28.57%, #5c93c6 94%);
  height: 423px;
  position: relative;
  // margin-bottom: 67px;
}

.footer-triangle {
  height: 423px;
  width: 1920px;
  background: linear-gradient(90.34deg, #020923 7.19%, #132b69 97.46%);
  clip-path: polygon(29% 0, 52% 52%, 29% 100%, 0 100%, 0 0);
}

.footer-content {
  position: absolute;
  top: 37%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 324px 80px 70px;
  width: 100%;
}

.footer-menu {
  display: flex;
  flex-direction: column;
}

.footer-link {
  text-decoration: none;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: #fff;
}

.footer-copyright {
  position: absolute;
  bottom: 20px;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  color: #fff;
  max-width: 580px;
  padding-left: 70px;
}

@media (max-width: 1800px) {
  .footer-triangle {
    clip-path: polygon(14% -1px, 28.2% 51.76%, 14% 100%, 0 100%, 0 0);
  }
}

@media (max-width: 1350px) {
  .footer-content {
    padding: 0 50px 80px 70px;
  }
}

@media (max-width: 1050px) {
  .footer-content {
    padding: 0 50px 80px 70px;
  }

  .footer-triangle {
    clip-path: polygon(6.5% 0, 16.23% 52.47%, 6.55% 99.76%, 0 100%, 0 0);
  }
}

@media (max-width: 992px) {
  .footer-content {
    padding: 0 20px 80px 20px;
  }

  .footer-link {
    font-size: 16px;
  }

  .footer-copyright {
    padding-left: 20px;
    max-width: 400px;
  }
}

@media (max-width: 768px) {
  .footer-menu {
    max-width: 300px;
  }
}

@media (max-width: 660px) {
  .footer-triangle {
    clip-path: polygon(0.35% 1px, 11.18% 49.4%, 0.3% 99.29%, 0 100%, 0 0);
  }

  .footer-menu {
    max-width: 200px;
  }

  .footer-copyright {
    font-size: 14px;
  }
}

@media (max-width: 450px) {
  .footer-logo img {
    width: 50%;
  }

  .footer-triangle {
    clip-path: polygon(0 0, 8% 29%, 0 65%, 0 21%, 0 0);
  }

  .footer-menu {
    max-width: 185px;
  }
}
