#confirm {
  background: #fff;
  position: absolute;
  z-index: 99999;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

.confirm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.confirm-title {
  font-size: 36px;
  line-height: 1.5;
  margin-bottom: 100px;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #777;
}

.confirm-question {
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 50px;
  color: #777;
  text-align: center;
}

.confirm-btn__block {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.confirm-btn {
  color: #fff;
  line-height: 100%;
  opacity: 1;
  padding: 0 70px;
  cursor: pointer;
  max-width: 212px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 24px;
}

.confirm-btn:hover {
  opacity: 0.7;
}

.confirm-btn:first-child {
  box-shadow: 0 15px 20px rgba(42, 146, 230, 0.4);
  background-color: #2a92e6;
  margin-right: 20px;
}

.confirm-btn:last-child {
  background-color: #00adbb;
  box-shadow: 0 15px 20px rgba(25, 181, 194, 0.4);
}

.confirm-warn {
  font-size: 16px;
  line-height: 24px;
  color: #777;
  text-align: center;
}

.confirm-cookie {
  max-width: 650px;
  text-align: center;
  margin-top: 50px;
}

@media (max-width: 390px) {
  .confirm-btn__block {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .confirm-btn {
    width: 200px;
  }

  .confirm-btn:first-child {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
