#new-taste {
  padding-top: 54px;
}

.new-taste {
  display: flex;
  align-items: end;
}

.new-taste__item {
  display: flex;
  flex-direction: column;
  max-width: 500px;
}

.subtitle-top {
  font-weight: bold;
  font-size: 23px;
  line-height: 40px;
  text-transform: uppercase;
  color: #000;
  position: relative;
  right: -2000px;
}

.title-top {
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  text-transform: uppercase;
  color: #122965;
  position: relative;
  right: -2000px;
}

.title-top__block {
  margin-bottom: 125px;
}

.jar-title {
  color: #231f20;
  max-width: 400px;
  margin-left: -2000px;
}

.jar-description {
  margin-left: -2000px;
}

.jar-subtitle {
  font-weight: normal;
  font-size: 40px;
  line-height: 40px;
  text-transform: uppercase;
  color: #37b0ec;
  margin-top: 29px;
  margin-bottom: 21px;
  position: relative;
  left: -2000px;
}

.taste {
  font-weight: normal;
  font-size: 23px;
  line-height: 28px;
  color: #000;
}

.new-tast__img {
  position: relative;
  right: -2000px;
}

.streight {
  font-weight: normal;
  font-size: 23px;
  line-height: 28px;
  color: #000;
}

.product-taste {
  text-transform: uppercase;
}

.new-taste__item:first-child {
  margin-right: 100px;
}

@media (max-width: 1100px) {
  .new-taste__item:first-child {
    margin-right: 0;
  }
}

@media (max-width: 992px) {
  #new-taste {
    padding-top: 0;
  }

  #new-taste::before {
    content: '';
    width: 100%;
    height: 100px;
    background: #4d81ba;
    display: block;
    margin-bottom: 20px;
    clip-path: polygon(100% 0, 100% 100%, 50% 33%, 0 100%, 0 0);
  }

  .title-top {
    font-size: 36px;
  }

  .jar-subtitle {
    font-size: 32px;
  }
}

@media (max-width: 768px) {
  .title-top {
    font-size: 27px;
  }
}

@media (max-width: 660px) {
  .new-taste {
    flex-wrap: wrap;
  }

  .new-taste__item {
    flex-direction: row;
    align-items: center;
  }

  .new-taste__item:last-child {
    order: -1;
  }
}

@media (max-width: 576px) {
  .subtitle-top {
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
  }

  .title-top {
    font-size: 20px;
    line-height: 26px;
  }
}
