#explanation-block {
  padding-top: 180px;
}

.explanation-item {
  background: #ededed;
  padding: 23px 0;
  margin-top: 8px;
}

.explanation-item__container {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #000;
  max-width: 1250px;
  margin: 0 auto;
}

@media (max-width: 992px) {
  #explanation-block {
    padding-top: 0;
  }

  .explanation-item__container {
    width: 95%;
  }
}
