.popup-menu {
  position: absolute;
  right: 0;
  top: -400px;
  background-color: #fff;
  padding: 25px 16px;
  margin-right: 0;
  min-width: 215px;
  z-index: 999;
  opacity: 0;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.popup-menu::before {
  content: '';
  position: absolute;
  top: -7px;
  right: 20px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 7px 10px;
  border-color: transparent transparent #fff transparent;
}

.popup-menu.active {
  opacity: 1;
  top: 100px;
}

.mobile-navbar {
  display: flex;
  flex-direction: column;
}

.mobile-navbar a {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 15px;
  letter-spacing: 0.5px;
  color: #041e42;
  text-decoration: none;
  padding: 20px;
}

.mobile-navbar a:hover {
  color: #00adbb;
}
