#inside-velo {
  padding-top: 200px;
}

.inside-velo__title {
  color: #fff;
  max-width: 310px;
  margin-left: -2000px;

  span {
    color: #ffff54;
  }
}

.inside-velo__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 70px;
  margin-top: 200px;
  opacity: 0;
}

@media (max-width: 992px) {
  #inside-velo {
    padding-top: 0;
    background: #0c2c76;
  }

  #inside-velo::before {
    content: '';
    width: 100%;
    height: 50px;
    background: #34b2ed;
    display: block;
    margin-bottom: 20px;
    clip-path: polygon(100% 0, 100% 100%, 30% 25%, 0 100%, 0 0);
  }

  .inside-velo__wrapper {
    justify-content: space-between;
  }

  .inside-velo__img {
    margin-bottom: 50px;
  }
}

.inside-velo__wrapper {
  overflow: visible !important;
}
.inside-velo-img-item1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inside-velo-img-item2 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inside-velo-img-item3 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inside-velo-img-item4 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inside-velo-img-item1::before {
  content: '';
  width: 133px;
  height: 38px;
  display: flex;
  background: url('../images/insideText1.png') no-repeat center top;
  position: absolute;
  top: -30px;
}

.inside-velo-img-item2::before {
  content: '';
  width: 133px;
  height: 38px;
  display: flex;
  background: url('../images/insideText2.png') no-repeat center top;
  position: absolute;
  top: -30px;
}

.inside-velo-img-item3::before {
  content: '';
  width: 275px;
  height: 150px;
  display: flex;
  background: url('../images/insideText3.png') no-repeat center top;
  position: absolute;
  top: -50px;
  left: 25px;
}

.inside-velo-img-item4::before {
  content: '';
  width: 275px;
  height: 150px;
  display: flex;
  background: url('../images/insideText4.png') no-repeat center top;
  position: absolute;
  top: -80px;
}

textPath {
  text-transform: uppercase;
  font-size: 23px;
}

@media (max-width: 690px) {
  // .inside-velo-img-item1 {
  //   width: 198px;
  // }

  // .inside-velo-img-item2 {
  //   width: 198px;
  // }

  // .inside-velo-img-item3 {
  //   width: 198px;
  // }

  .inside-velo-img-item4 {
    width: 198px;
  }

  .swiper-slide > .inside-velo-img-item1::before {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide > .inside-velo-img-item2::before {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide > .inside-velo-img-item3::before {
    opacity: 0;
    transition: 0.5s;
    left: -5px;
  }

  .swiper-slide > .inside-velo-img-item4::before {
    opacity: 0;
    transition: 0.5s;
  }

  
  .swiper-slide-active > .inside-velo-img-item1::before {
    opacity: 1;
  }

  .swiper-slide-active > .inside-velo-img-item2::before {
    opacity: 1;
  }

  .swiper-slide-active > .inside-velo-img-item3::before {
    opacity: 1;
  }

  .swiper-slide-active > .inside-velo-img-item4::before {
    opacity: 1;
  }
}

@media (max-width: 445px) {
  .inside-velo-img-item1 {
    width: 100%;
  }

  .inside-velo-img-item2 {
    width: 100%;
  }

  .inside-velo-img-item3 {
    width: 100%;
  }

  .inside-velo-img-item4 {
    width: 100%;
  }
}

@media (max-width: 1300px) {
  .inside-velo-img-item3::before {
    left: 0;
  }
}

@media (max-width: 810px) and (min-width: 800px) {
  .inside-velo-img-item3::before {
    left: -20px;
  }
}

@media (max-width: 769px) and (min-width: 700px) {
  .inside-velo-img-item3::before {
    left: -10px;
  }
}

@media (max-width: 470px) and (min-width: 440px) {
  .inside-velo-img-item3::before {
    left: 16px;
  }
}

@media (max-width: 440px) and (min-width: 400px) {
  .swiper-slide > .inside-velo-img-item3::before {
    left: 8px;
  }
}

@media (max-width: 400px) and (min-width: 350px) {
  .swiper-slide > .inside-velo-img-item3::before {
    left: 0;
  }
}

@media (max-width: 350px) and (min-width: 320px) {
  .swiper-slide > .inside-velo-img-item3::before {
    left: -20px;
  }
}


