#main-bg {
  width: 100%;
}

.top-block {
  background: url('../images/main-bg/lastScreen/main-bg.png') no-repeat center top / cover;
  height: calc(100vh - 200px);
  overflow: hidden;
  position: relative;
}

.main-bg__position {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: calc(100vh - 70px);
}

.jar-block {
  position: absolute;
}

.last-screen__ice-top_block {
  position: absolute;
  transition: 1s;
}

.last-screen__ice-top_block.active {
  transform: translate(350px, -350px);
}

.last-screen__ice-bottom_block {
  position: absolute;
  transition: 1s;
}

.last-screen__ice-bottom_block.active {
  transform: translate(-300px, 260px);
}

.last-screen__triangle {
  position: absolute;
  left: -50%;
  top: 40px;
  transition: 1s;
}

.last-screen__triangle.active {
  left: -10px;
}

.last-screen__text {
  position: absolute;
  right: 0;
  transform: translate(300px, -300px);
  transition: 1s;
}

.last-screen__text.active {
  transform: translate(0, 0);
  transition: 1s;
}

.first-screen__ice1 {
  position: absolute;
  bottom: 0;
  transition: 1s;
}

.first-screen__ice1.active {
  transform: translate(-330px, 330px);
}

.first-screen__ice3 {
  position: absolute;
  bottom: 0;
  right: 0;
  transition: 1s;
}

.first-screen__ice3.active {
  transform: translate(500px, 500px);
}

.first-screen__ice2 {
  position: absolute;
  bottom: 0;
  right: 0;
  transition: 1s;
}

.first-screen__ice2.active {
  transform: translate(-470px, 300px) rotate(65deg);
}

.first-screen__ice2.active.active-last {
  transform: translate(-850px, 600px) rotate(65deg);
}

.first-screen__ice7 {
  position: absolute;
  left: 0;
  transition: all 1s;
}

.first-screen__ice7.active {
  transform: translate(-330px, 0);
  opacity: 0;
}

.first-screen__ice9 {
  position: absolute;
  bottom: 0;
  left: 30%;
  transition: 1s;
}

.first-screen__ice10 {
  position: absolute;
  right: 0;
  bottom: 16.5%;
  transition: 1s;
}

.first-screen__ice10.active {
  transform: translate(100%, 0);
  opacity: 0;
}

.first-screen__ice8 {
  position: absolute;
  right: 0;
  transition: 1s;
}

.first-screen__ice8.active {
  width: 40%;
  transform: translate(0, 250px);
}

.first-screen__ice8.active.active-last {
  width: 40%;
  transform: translate(800px, 250px) rotate(40deg);
  opacity: 0;
}

.first-screen__ice6 {
  position: absolute;
  left: -60px;
  transition: 1s;
}

.first-screen__ice6.active {
  transform: translate(-250px, -200px);
}

.first-screen__ice6.active.active-last {
  transform: translate(-450px, -400px);
}

.first-screen__ice5 {
  position: absolute;
  left: -20px;
  transition: 1s;
}

.first-screen__ice5.active {
  transform: translate(300px, -150px);
  opacity: 0;
}

.first-screen__ice9.active {
  transform: translate(100px, 150px);
}

.first-screen__ice9.active.active-last {
  transform: translate(140px, 450px);
}

.first-screen__ice4 {
  position: absolute;
  left: 50%;
  top: 43%;
  transition: 1s;
}

.first-screen__ice4.active {
  left: 70%;
  top: 50%;
  transition: 1s;
}

.first-screen__ice4.active-last {
  top: 40%;
  left: 76%;
  transition: 1s;
}

.first-screen__bigIce1 {
  position: absolute;
  right: -550px;
  transition: 1s;
}

.first-screen__bigIce1.active {
  transform: translate(200px, -100px);
  transition: 1s;
  opacity: 0;
  width: 90%;
}

.first-screen__bigIce2 {
  position: absolute;
  left: 0;
  transition: 1s;
}

.first-screen__bigIce2.active {
  transform: translate(-300px, -90px);
  opacity: 0;
  width: 90%;
}

@media (max-width: 1700px) {
  .last-screen__triangle {
    top: 148px;
    width: 42%;
  }

  .last-screen__text {
    width: 33%;
  }
}

@media (max-width: 1300px) {
  .last-screen__triangle {
    top: 180px;
  }
}

@media (max-width: 1200px) {
  .jar-block img {
    width: 50%;
  }
}

@media (max-width: 992px) {
  .last-screen__triangle {
    top: 234px;
  }
}

@media (max-width: 768px) {
  .last-screen__triangle {
    top: 272px;
    width: 46%;
  }
}

@media (max-width: 660px) {
  .jar-block img {
    width: 65%;
  }
}

@media (max-width: 576px) {
  .jar-block img {
    width: 80%;
  }

  .last-screen__triangle {
    width: 80%;
  }

  .last-screen__text {
    width: 74%;
  }

  .last-screen__ice-bottom_block.active {
    transform: translate(60px, 262px) rotate(-30deg);
  }

  .last-screen__ice-top_block.active {
    transform: translate(-79px, -350px);
  }

  .first-screen__ice10 {
    bottom: 10.5%;
  }

  .first-screen__ice1 {
    bottom: 235px;
  }
}

@media (max-width: 460px) {
  .first-screen__ice9 {
    bottom: 21%;
    left: -13%;
  }

  .first-screen__ice7 {
    top: 123px;
  }

  .first-screen__ice6 {
    top: 10%;
  }

  .first-screen__ice5 {
    top: 10%;
  }

  .first-screen__ice8 {
    transform: rotate(48deg);
    z-index: 1;
  }

  .first-screen__ice2 {
    position: absolute;
    bottom: -18%;
    right: -5px;
    transition: 1s;
    left: -50%;
    transform: rotate(90deg);
  }
}

@media (max-width: 390px) {
  .jar-block img {
    width: 95%;
  }

  .last-screen__triangle {
    top: 238px;
    width: 100%;
  }

  .last-screen__text {
    width: 100%;
  }

  .first-screen__ice2 {
    bottom: -2%;
  }

  .first-screen__ice10 {
    bottom: 6.5%;
    transform: rotate(-11deg);
    right: -70px;
  }

  .first-screen__ice1 {
    bottom: 214px;
    transform: rotate(-10deg);
    right: -137px;
  }

  .first-screen__ice7 {
    top: 137px;
    transform: rotate(5deg);
  }

  .first-screen__ice8 {
    transform: rotate(40deg);
    z-index: 1;
    top: 60px;
  }
}

@media (max-height: 850px) {
  .top-block {
    background: url('../images/main-bg/lastScreen/main-bg.png') no-repeat center top / cover;
    height: calc(100vh - 100px);
    overflow: hidden;
    position: relative;
  }
}

@media (max-height: 750px) {
  .top-block {
    background: url('../images/main-bg/lastScreen/main-bg.png') no-repeat center top / cover;
    height: calc(100vh - 50px);
    overflow: hidden;
    position: relative;
  }

  .jar-block img {
    width: 40%;
  }
}

@media (max-height: 680px) {
  .last-screen__triangle {
    top: 179px;
    width: 75%;
  }

  .jar-block img {
    width: 70%;
  }
}

@media (max-width: 576px) {
  .jar-block img {
    // max-width: 100% !important;
    max-width: 65%;
    width: initial;
  }

  .top-block {
    background: url(../images/mobile_bg.png) no-repeat center top / cover;
  }

  .last-screen__triangle {
    // top: 50% !important;
    width: 50%;
    top: 46% !important;
  }

  .main-bg__position {
    height: auto;
    top: 40%;
  }

  .last-screen__text.active {
    // transform: translate(-61px, 99px);
    transform: translate(26px, 42px);
  }

  .last-screen__text {
    width: initial;
  }
}